.search__container {
  width: 100%;
}


.search {
  width: 100%;
  position: relative
}

.searchTerm {
  float: left;
  width: 100%;
  border: 3px solid #00B4CC;
  padding: 5px;
  height: 20px;
  border-radius: 5px;
  outline: none;
  color: #9DBFAF;
}

.searchTerm:focus{
  color: #00B4CC;
}

.searchButton {
  position: absolute;  
  right: -50px;
  width: 40px;
  height: 36px;
  border: 1px solid #00B4CC;
  background: #00B4CC;
  text-align: center;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  font-size: 20px;
}

  .input__list{
    list-style: none;
    -webkit-transition: all 500ms ease-in-out;
    transition: all 500ms ease-in-out;
  }

 .input__list li{
    cursor: pointer;
    border-radius: 5px;
    padding: 5px;
    background: #fff;
    margin-bottom: 2px;
  }
  .input__list:before{
    content: 'Options';
    font-size:16px;
    font-weight: bolder;

  }
  li:hover{
    color: #0575e6;
    background: #fff;
  }
  .input__list li:last-child{
    cursor:none;
    border-radius:none;
    padding: 5px;
    background: none;
    margin-bottom: 0px;
    border: none;
    color: gray;
  }
  .input__list li:hover:last-child{
    color:gray;
    
  }



.bar {
  background: rgba(255, 255, 255, 0.5);
  content: '';
  width: 550px;
  height: 3.6666666667px;
  -webkit-transition: .3s ease;
  transition: .3s ease;
  position: relative;
}
.bar:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 150%;
  background: #2196f3;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

::-moz-selection {
  background: rgba(33, 150, 243, 0.3);
}

::selection {
  background: rgba(33, 150, 243, 0.3);
}


